import JsBarcode from 'jsbarcode';

class BarcodeManager {
  constructor() {
    this.defaultOptions = {
      format: 'CODE128',
      width: 2,
      height: 100,
      displayValue: true,
      fontSize: 12,
      textAlign: 'center',
      textPosition: 'bottom',
      textMargin: 2,
      fontOptions: '',
      font: 'monospace',
      background: '#ffffff',
      lineColor: '#000000',
      margin: 10
    };
  }

  // Generate a unique product code
  generateProductCode(prefix = 'PS') {
    const timestamp = Date.now().toString(36);
    const random = Math.random().toString(36).substr(2, 5);
    return `${prefix}${timestamp}${random}`.toUpperCase();
  }

  // Generate barcode as Data URL
  generateBarcodeDataURL(code, options = {}) {
    try {
      const canvas = document.createElement('canvas');
      const mergedOptions = { ...this.defaultOptions, ...options };
      
      JsBarcode(canvas, code, mergedOptions);
      
      return canvas.toDataURL('image/png');
    } catch (error) {
      console.error('Error generating barcode:', error);
      return null;
    }
  }

  // Generate barcode as SVG string
  generateBarcodeSVG(code, options = {}) {
    try {
      const svg = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
      const mergedOptions = { ...this.defaultOptions, ...options };
      
      JsBarcode(svg, code, mergedOptions);
      
      return new XMLSerializer().serializeToString(svg);
    } catch (error) {
      console.error('Error generating SVG barcode:', error);
      return null;
    }
  }

  // Generate barcode and render to existing element
  renderBarcode(element, code, options = {}) {
    try {
      const mergedOptions = { ...this.defaultOptions, ...options };
      JsBarcode(element, code, mergedOptions);
      return true;
    } catch (error) {
      console.error('Error rendering barcode:', error);
      return false;
    }
  }

  // Validate barcode format
  isValidCode(code, format = 'CODE128') {
    try {
      const canvas = document.createElement('canvas');
      JsBarcode(canvas, code, { format, valid: () => true });
      return true;
    } catch (error) {
      return false;
    }
  }

  // Generate multiple barcodes for batch processing
  generateBatchBarcodes(items, options = {}) {
    const results = [];
    
    items.forEach(item => {
      try {
        const code = item.barcode || this.generateProductCode();
        const dataURL = this.generateBarcodeDataURL(code, options);
        
        results.push({
          ...item,
          barcode: code,
          barcodeImage: dataURL,
          success: true
        });
      } catch (error) {
        results.push({
          ...item,
          success: false,
          error: error.message
        });
      }
    });
    
    return results;
  }

  // Create barcode for printing (high resolution)
  generatePrintBarcode(code, options = {}) {
    const printOptions = {
      ...this.defaultOptions,
      width: 3,
      height: 150,
      fontSize: 14,
      margin: 15,
      ...options
    };
    
    return this.generateBarcodeDataURL(code, printOptions);
  }

  // Generate EAN-13 barcode (for retail products)
  generateEAN13(code) {
    // Ensure code is 12 digits, calculate check digit
    if (code.length === 12) {
      const checkDigit = this.calculateEAN13CheckDigit(code);
      const fullCode = code + checkDigit;
      
      return this.generateBarcodeDataURL(fullCode, {
        format: 'EAN13',
        width: 2,
        height: 100
      });
    }
    
    throw new Error('EAN-13 requires 12 digits');
  }

  // Calculate EAN-13 check digit
  calculateEAN13CheckDigit(code) {
    let sum = 0;
    for (let i = 0; i < 12; i++) {
      const digit = parseInt(code[i]);
      sum += (i % 2 === 0) ? digit : digit * 3;
    }
    return (10 - (sum % 10)) % 10;
  }

  // Generate UPC-A barcode
  generateUPCA(code) {
    if (code.length === 11) {
      const checkDigit = this.calculateUPCACheckDigit(code);
      const fullCode = code + checkDigit;
      
      return this.generateBarcodeDataURL(fullCode, {
        format: 'UPC',
        width: 2,
        height: 100
      });
    }
    
    throw new Error('UPC-A requires 11 digits');
  }

  // Calculate UPC-A check digit
  calculateUPCACheckDigit(code) {
    let oddSum = 0;
    let evenSum = 0;
    
    for (let i = 0; i < 11; i++) {
      const digit = parseInt(code[i]);
      if (i % 2 === 0) {
        oddSum += digit;
      } else {
        evenSum += digit;
      }
    }
    
    const total = (oddSum * 3) + evenSum;
    return (10 - (total % 10)) % 10;
  }

  // Get supported barcode formats
  getSupportedFormats() {
    return [
      'CODE128',
      'CODE39',
      'EAN13',
      'EAN8',
      'UPC',
      'ITF14',
      'MSI',
      'pharmacode',
      'codabar'
    ];
  }

  // Get barcode format info
  getFormatInfo(format) {
    const formats = {
      'CODE128': {
        name: 'Code 128',
        description: 'High-density linear barcode, supports all ASCII characters',
        minLength: 1,
        maxLength: null,
        checksum: true
      },
      'CODE39': {
        name: 'Code 39',
        description: 'Alphanumeric barcode, widely used in industry',
        minLength: 1,
        maxLength: null,
        checksum: false
      },
      'EAN13': {
        name: 'EAN-13',
        description: 'European Article Number, 13 digits',
        minLength: 13,
        maxLength: 13,
        checksum: true
      },
      'EAN8': {
        name: 'EAN-8',
        description: 'European Article Number, 8 digits',
        minLength: 8,
        maxLength: 8,
        checksum: true
      },
      'UPC': {
        name: 'UPC-A',
        description: 'Universal Product Code, 12 digits',
        minLength: 12,
        maxLength: 12,
        checksum: true
      }
    };
    
    return formats[format] || null;
  }
}

export default BarcodeManager;
