import html2canvas from 'html2canvas';

class ExportUtils {
  constructor() {
    this.isElectron = typeof window !== 'undefined' && window.electronAPI;
  }

  // Export price sheet as image using Electron's native capture
  async exportPriceSheetElectron(options = {}) {
    if (!this.isElectron) {
      throw new Error('Electron API not available');
    }

    try {
      const result = await window.electronAPI.exportImage(options);
      return result;
    } catch (error) {
      console.error('Electron export error:', error);
      throw error;
    }
  }

  // Export price sheet as image using html2canvas (fallback)
  async exportPriceSheetCanvas(elementId = 'price-sheet-preview', options = {}) {
    try {
      const element = document.getElementById(elementId);
      if (!element) {
        throw new Error(`Element with ID '${elementId}' not found`);
      }

      const defaultOptions = {
        backgroundColor: '#ffffff',
        scale: 2, // Higher resolution
        useCORS: true,
        allowTaint: true,
        logging: false,
        width: element.scrollWidth,
        height: element.scrollHeight
      };

      const mergedOptions = { ...defaultOptions, ...options };
      const canvas = await html2canvas(element, mergedOptions);
      
      return {
        canvas,
        dataURL: canvas.toDataURL('image/png', 1.0),
        blob: await this.canvasToBlob(canvas)
      };
    } catch (error) {
      console.error('Canvas export error:', error);
      throw error;
    }
  }

  // Convert canvas to blob
  canvasToBlob(canvas, type = 'image/png', quality = 1.0) {
    return new Promise((resolve) => {
      canvas.toBlob(resolve, type, quality);
    });
  }

  // Download image in browser
  downloadImage(dataURL, filename = 'price-sheet.png') {
    try {
      const link = document.createElement('a');
      link.download = filename;
      link.href = dataURL;
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
      return true;
    } catch (error) {
      console.error('Download error:', error);
      return false;
    }
  }

  // Export with automatic method selection
  async exportPriceSheet(elementId, options = {}) {
    try {
      if (this.isElectron) {
        // Use Electron's native capture for better quality
        return await this.exportPriceSheetElectron(options);
      } else {
        // Fallback to html2canvas
        const result = await this.exportPriceSheetCanvas(elementId, options);
        
        // Auto-download in browser
        const filename = options.filename || `price-sheet-${new Date().toISOString().split('T')[0]}.png`;
        this.downloadImage(result.dataURL, filename);
        
        return { success: true, method: 'canvas', dataURL: result.dataURL };
      }
    } catch (error) {
      console.error('Export error:', error);
      return { success: false, error: error.message };
    }
  }

  // Print labels using Electron
  async printLabelsElectron(items) {
    if (!this.isElectron) {
      throw new Error('Electron API not available');
    }

    try {
      const result = await window.electronAPI.printLabels(items);
      return result;
    } catch (error) {
      console.error('Electron print error:', error);
      throw error;
    }
  }

  // Print labels using browser print
  printLabelsBrowser(items, options = {}) {
    try {
      const printWindow = window.open('', '_blank');
      if (!printWindow) {
        throw new Error('Could not open print window');
      }

      const labelHtml = this.generateLabelHTML(items, options);
      
      printWindow.document.write(labelHtml);
      printWindow.document.close();
      
      // Wait for content to load then print
      printWindow.onload = () => {
        setTimeout(() => {
          printWindow.print();
          printWindow.close();
        }, 500);
      };

      return { success: true };
    } catch (error) {
      console.error('Browser print error:', error);
      return { success: false, error: error.message };
    }
  }

  // Generate HTML for label printing
  generateLabelHTML(items, options = {}) {
    const {
      labelWidth = '4in',
      labelHeight = '2in',
      fontSize = '12px',
      margin = '0.1in',
      padding = '0.2in'
    } = options;

    const labelsHtml = items.map(item => `
      <div class="label" data-item-id="${item.id}">
        <div class="barcode-container">
          <canvas class="barcode-canvas" data-barcode="${item.barcode}"></canvas>
        </div>
        <div class="item-name">${item.name}</div>
        <div class="item-price">$${parseFloat(item.price).toFixed(2)}</div>
        ${item.brand ? `<div class="item-brand">${item.brand}</div>` : ''}
        ${item.model ? `<div class="item-model">${item.model}</div>` : ''}
      </div>
    `).join('');

    return `
      <!DOCTYPE html>
      <html>
      <head>
        <title>Price Labels</title>
        <style>
          @page {
            margin: 0.5in;
            size: letter;
          }
          
          body {
            margin: 0;
            padding: 0;
            font-family: Arial, sans-serif;
            display: flex;
            flex-wrap: wrap;
            gap: ${margin};
          }
          
          .label {
            width: ${labelWidth};
            height: ${labelHeight};
            border: 1px solid #000;
            padding: ${padding};
            margin: ${margin};
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            text-align: center;
            page-break-inside: avoid;
            box-sizing: border-box;
          }
          
          .barcode-container {
            margin-bottom: 8px;
            max-height: 60px;
            overflow: hidden;
          }
          
          .barcode-canvas {
            max-width: 100%;
            height: auto;
          }
          
          .item-name {
            font-weight: bold;
            font-size: ${fontSize};
            margin-bottom: 4px;
            line-height: 1.2;
          }
          
          .item-price {
            font-size: calc(${fontSize} * 1.2);
            font-weight: bold;
            color: #000;
            margin-bottom: 2px;
          }
          
          .item-brand, .item-model {
            font-size: calc(${fontSize} * 0.8);
            color: #666;
            margin-bottom: 1px;
          }
          
          @media print {
            body { 
              -webkit-print-color-adjust: exact;
              print-color-adjust: exact;
            }
            
            .label {
              break-inside: avoid;
            }
          }
        </style>
        <script src="https://cdn.jsdelivr.net/npm/jsbarcode@3.11.5/dist/JsBarcode.all.min.js"></script>
      </head>
      <body>
        ${labelsHtml}
        
        <script>
          // Generate barcodes after page loads
          window.onload = function() {
            const canvases = document.querySelectorAll('.barcode-canvas');
            canvases.forEach(canvas => {
              const barcode = canvas.getAttribute('data-barcode');
              if (barcode && window.JsBarcode) {
                try {
                  JsBarcode(canvas, barcode, {
                    format: 'CODE128',
                    width: 1.5,
                    height: 40,
                    displayValue: false,
                    margin: 0
                  });
                } catch (error) {
                  console.error('Barcode generation error:', error);
                }
              }
            });
          };
        </script>
      </body>
      </html>
    `;
  }

  // Print labels with automatic method selection
  async printLabels(items, options = {}) {
    try {
      if (this.isElectron) {
        return await this.printLabelsElectron(items);
      } else {
        return this.printLabelsBrowser(items, options);
      }
    } catch (error) {
      console.error('Print labels error:', error);
      return { success: false, error: error.message };
    }
  }

  // Save project data
  async saveProject(projectData) {
    try {
      if (this.isElectron) {
        return await window.electronAPI.saveProject(projectData);
      } else {
        // Browser fallback - use localStorage or download as JSON
        const dataStr = JSON.stringify(projectData, null, 2);
        const filename = `project-${new Date().toISOString().split('T')[0]}.json`;
        
        const blob = new Blob([dataStr], { type: 'application/json' });
        const url = URL.createObjectURL(blob);
        
        this.downloadImage(url, filename);
        URL.revokeObjectURL(url);
        
        return { success: true, method: 'download' };
      }
    } catch (error) {
      console.error('Save project error:', error);
      return { success: false, error: error.message };
    }
  }

  // Load project data
  async loadProject() {
    try {
      if (this.isElectron) {
        return await window.electronAPI.loadProject();
      } else {
        // Browser fallback - use file input
        return new Promise((resolve) => {
          const input = document.createElement('input');
          input.type = 'file';
          input.accept = '.json';
          
          input.onchange = (event) => {
            const file = event.target.files[0];
            if (file) {
              const reader = new FileReader();
              reader.onload = (e) => {
                try {
                  const data = JSON.parse(e.target.result);
                  resolve({ success: true, data });
                } catch (error) {
                  resolve({ success: false, error: 'Invalid JSON file' });
                }
              };
              reader.readAsText(file);
            } else {
              resolve({ success: false, error: 'No file selected' });
            }
          };
          
          input.click();
        });
      }
    } catch (error) {
      console.error('Load project error:', error);
      return { success: false, error: error.message };
    }
  }

  // Get export capabilities
  getCapabilities() {
    return {
      isElectron: this.isElectron,
      canExportImage: true,
      canPrintLabels: true,
      canSaveProject: true,
      canLoadProject: true,
      supportedFormats: this.isElectron ? ['png', 'jpg'] : ['png'],
      nativeFileDialog: this.isElectron
    };
  }
}

export default ExportUtils;
